.GlitterAnimate {
  position: relative;
  background-image: url("../../images/GlitterAnimate.jpg");
}

.GlitterAnimate__movement {
  width: 10px;
  height: 10px;
  position: absolute;
  top: 50%;
  left: 37%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  background-image: url("../../images/Glitter.png");
  background-size: cover;
  border-radius: 25%;
}

.GlitterAnimate__movement--one {
  left: 33%;
  -webkit-animation: movement 4s ease-in infinite;
          animation: movement 4s ease-in infinite;
}

.GlitterAnimate__movement--two {
  left: 42%;
  -webkit-animation: movement 3.75s ease-in infinite;
          animation: movement 3.75s ease-in infinite;
}

.GlitterAnimate__movement--three {
  left: 34%;
  -webkit-animation: movement 3.5s ease-in infinite;
          animation: movement 3.5s ease-in infinite;
}

.GlitterAnimate__movement--four {
  left: 41%;
  -webkit-animation: movement 3.25s ease-in infinite;
          animation: movement 3.25s ease-in infinite;
}

.GlitterAnimate__movement--five {
  left: 35%;
  -webkit-animation: movement 3s ease-in infinite;
          animation: movement 3s ease-in infinite;
}

.GlitterAnimate__movement--six {
  left: 40%;
  -webkit-animation: movement 2.75s ease-in infinite;
          animation: movement 2.75s ease-in infinite;
}

.GlitterAnimate__movement--seven {
  left: 36%;
  -webkit-animation: movement 2.5s ease-in infinite;
          animation: movement 2.5s ease-in infinite;
}

.GlitterAnimate__movement--eight {
  left: 39%;
  -webkit-animation: movement 2.25s ease-in infinite;
          animation: movement 2.25s ease-in infinite;
}

.GlitterAnimate__movement--nine {
  left: 37%;
  -webkit-animation: movement 2s ease-in infinite;
          animation: movement 2s ease-in infinite;
}

.GlitterAnimate__movement--ten {
  left: 33.5%;
  -webkit-animation: movement 1.75s ease-in infinite;
          animation: movement 1.75s ease-in infinite;
}

.GlitterAnimate__movement--eleven {
  left: 40.5%;
  -webkit-animation: movement 1.5s ease-in infinite;
          animation: movement 1.5s ease-in infinite;
}

.GlitterAnimate__movement--twelve {
  left: 39.5%;
  -webkit-animation: movement 1.25s ease-in infinite;
          animation: movement 1.25s ease-in infinite;
}

.GlitterAnimate__movement--thirteen {
  left: 43.5%;
  -webkit-animation: movement 1s ease-in infinite;
          animation: movement 1s ease-in infinite;
}

.GlitterAnimate__movement--fourteen {
  left: 40.5%;
  -webkit-animation: movement 0.75s ease-in infinite;
          animation: movement 0.75s ease-in infinite;
}

@-webkit-keyframes movement {
  from {
    top: 30%;
  }
  to {
    top: 90%;
  }
}

@keyframes movement {
  from {
    top: 30%;
  }
  to {
    top: 90%;
  }
}



