.Glitch {
  font-family: "Roboto", sans-serif;
  background-color: #fbe9dc;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.glitch {
  color: white;
  font-size: 60px;
  text-transform: upercase;
  position: relative;
  display: inline-block;
}

.glitch::before,
.glitch::after {
  content: attr(data-text);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #fbe9dc;
}
.glitch::before {
  left: 2px;
  text-shadow: -2px 0 grey;
  clip: rect(24px, 550px, 90px, 0);
  -webkit-animation: glitch-anim-2 3s infinite linear alternate-reverse;
          animation: glitch-anim-2 3s infinite linear alternate-reverse;
}
.glitch::after {
  left: -2px;
  text-shadow: -2px 0 darkgrey;
  clip: rect(85px, 550px, 140px, 0);
  -webkit-animation: glitch-anim 2.5s infinite linear alternate-reverse;
          animation: glitch-anim 2.5s infinite linear alternate-reverse;
}

